import React, { useState } from 'react';
import { db } from '../firebase';
import { ref, push } from 'firebase/database';


const FirebaseEventPusher = () => {
    const [eventData, setEventData] = useState({});

    const handleChange = (event) => {
        setEventData({
            ...eventData,
            [event.target.name]: event.target.value
        });
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        const eventsRef = db.database().ref('events');
        eventsRef.push(eventData);
        setEventData({});
    };

    return (
        <form onSubmit={handleSubmit}>
            <label>
                Event Name:
                <input type="text" name="name" onChange={handleChange} value={eventData.name || ''} />
            </label>
            <label>
                Event Date:
                <input type="text" name="date" onChange={handleChange} value={eventData.date || ''} />
            </label>
            <button type="submit">Submit</button>
        </form>
    );
};

export default FirebaseEventPusher;
